import React from 'react'
import './index.less'
import {Avatar, Button, Modal, Spin} from 'antd';
import axios from "axios";
import {baseUrl} from "../../../../config";
import moment from 'moment'
import {Link} from "react-router-dom";

export default class TaskPosted extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs: [],
            idActive: 0,
            visibleCommunications: false,
        }
    }

    componentWillMount() {
        console.log(this.state);
        this.getFav()
    }

    getCommunications = (id) => {
        const ws = this.state.myFavs;
        for (const w in  ws) {
            const p = ws[w];
            console.log(p);
            if (p.id === id) {
                // console.log('avl', p);
                return (
                    <div>
                        <h2 className='name'>手机号:{p.communications.phone}</h2>
                        <img src={`${baseUrl}/${p.communications.wx_png}`} alt=''
                             className='avatar'
                             width='100%'
                        />
                        <h5>提示：长按识别二维码添加微信</h5>
                    </div>
                )
            }
        }
    };
    getFav = () => {
        // 有无缓存？返回：请求->缓存->返回
        // const cached = localStorage.works_by_me!==undefined;
        const cached = false;

        if(cached){
            // 返回数据
            this.setState({
                myFavs:JSON.parse(localStorage.works_by_me),
            })
        }else {
            // 请求
            axios.get(
                baseUrl + '/api/get_works_by_me',
                {
                    params: {
                        token: localStorage.customer_token,
                    }
                })
                .then((Response) => {
                    console.log('请求了works_by_me')
                    this.setState({
                        myFavs: Response.data.works,
                    });
                    // 设置缓存
                    localStorage.works_by_me = JSON.stringify(Response.data.works)
                });
        }
    };
    showCommunications = (id) => {
        console.log(id);
        this.setState({
            visibleCommunications: true,
            idActive: id,
        });
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    type_convert = (type) => {
        switch (type) {
            case 1:
                return "代办类";
            case 2:
                return "转发类";
            case 3:
                return "问卷类";
            case 0:
                return "其他";
            default:
                return "全部";
        }
    };
    renderFav = () => {
        if (this.state.myFavs.length === 0) {
            return <h2>暂无</h2>
        }
        let fs = [];
        const ws = this.state.myFavs;
        for (const w in  ws) {
            const p = ws[w];
            fs.push(
                <li className="cell">
                    <div className='cell-index' id={p.id}>
                        <span className='right-font'>{this.type_convert(p.work_tpl)}</span>

                        {/*头像*/}
                        <div className='avatar-name'>
                            <Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}
                                    className='avatar'>头像</Avatar>
                            <span className='name'>{p.senter_name}</span>
                            <div className='money'>
                                <span className='work_money'>¥{p.work_money !== 0 ? p.work_money : '可以协商'}</span>
                            </div>
                            {/*<span className='work_tpl'>{this.type_convert(p.work_tpl)}</span>*/}
                        </div>
                        <div className='main-issue'>
                            <div>

                                <span className='work_name'>{p.work_name}</span>
                                <span className='persons'>/{p.work_person_number}人</span>
                                <div className='descriptions'>
                                    {p.work_content ? <div>{p.work_content}</div> : null}
                                    {p.work_url ? <a href={p.work_url}>相关链接</a> : null}
                                </div>
                            </div>
                            <div>

                                <div className='time-shit'>
                                    {/*{p.work_time ? <div>发布时间:{moment.unix(p.work_time).format("YYYY-MM-DD HH:mm")}</div>:null}*/}
                                    {/*<br/>*/}
                                    {/*完成任务限定时间:*/}
                                    {/*<br/>*/}
                                    {/*{moment.unix(p.begin_work_time).format("YYYY-MM-DD HH:mm")}~{moment.unix(p.stop_work_time).format("YYYY-MM-DD HH:mm")}*/}
                                    {p.stop_at ?
                                        <div>截止:{
                                            moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                                                '今天内'
                                                : moment.unix(p.stop_at).format("MM-DD HH:mm")
                                        }
                                        </div>
                                        : null}
                                </div>

                            </div>
                            <div className='user-actions'>
                                <Link to={`/w/${p.id}`}>
                                    查看详情
                                </Link>
                            </div>
                        </div>

                        <div className='btns-shit'>
                            {/*<Button type='primary' onClick={this.working.bind(this, p.id)}>接单</Button>*/}
                            {/*<Button type='primary' onClick={this.showCommunications.bind(this, p.id)}>联系</Button>*/}
                        </div>
                    </div>
                </li>
            )
        }

        return fs
    };

    render() {
        return (
            this.state.myFavs !== undefined
                ?
                <div className='my-favs'>
                    {this.renderFav()}
                    <Modal
                        visible={this.state.visibleCommunications}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        style={{textAlign: 'center'}}
                    >
                        {this.getCommunications(this.state.idActive)}
                    </Modal>
                </div>
                : <Spin/>

        )

    }
}
